/*****************
Left Drawer Menu -new
*****************/
.mx-layout-left-drawer-tm {
    width: 50px;
    height: calc(100% - 62px);
    max-height: 100%;
    position: fixed;
    bottom: 0px;
    top: 63px;
    left: 0;
    box-shadow: 0 -2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);
    box-sizing: border-box;
    /* background: #ffffff; */
    background: linear-gradient(to bottom, #504E77, #6D6CA3);
    color: #ffffff;
    z-index: 11;
    padding: 0px;
}
.mx-layout-left-drawer-tm a {
    color: #ffffff;
}
.mx-left-drawer-menu {
    display: block;
    padding: 0px;
    /* border-bottom: 1px solid #ccc; */
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.3);
    height: 50px;
    width: 100%;
    max-width: 300px;
}
.mx-left-drawer-menu.active i {
    color: #545586;
    background-color: #E5E5ED;
    padding: 4px;
    border-radius: 4px;
}
.mx-left-drawer-menu>span.material-icons.mx-leftnav-icon,
.mx-layout-left-drawer-tm>span.material-icons.mx-leftnav-icon {
    width: 50px;
    height: 50px;
    text-align: center;
    padding: 8px;
}
.mx-left-drawer-menu.active>.mx-menu-icon-wrapper>span.material-icons.mx-leftnav-icon,
.mx-layout-left-drawer-tm.active>span.material-icons.mx-leftnav-icon {
    color: #545586;
    /* background-color: #E5E5ED; 
    border-radius: 4px;
    margin-left: 4px;
    margin-top: 4px;
    width: 40px;
    height: 40px */
}
.mx-left-drawer-menu.active>.mx-menu-icon-wrapper,
.mx-layout-left-drawer-tm.active>span.material-icons.mx-leftnav-icon {
    background-color: #E5E5ED;
}
.mx-left-drawer-menu>.mx-menu-icon-wrapper>span.material-icons.mx-leftnav-icon {
    margin: 5px;
}
.mx-menu-icon-wrapper {
    width: 34px;
    height: 34px;
    margin: 8px;
    float: left;
    border-radius: 3px;
    padding: 2px;
}
.mx-left-drawer-menu.active span>i {
    background: none;
    color: #fff;
    padding-bottom: 0px;
}
.mx-left-drawer-tm-menu {
    display: block;
    /* padding: 12px 10px; */
    border-bottom: 1px solid #ccc;
    height: 100%;
    min-height: 50px;
    font-weight: 300;
}
.mx-left-drawer-tm-menu.active {
    display: block;
    border-bottom: 1px solid #ccc;
    color: #7676b8 !important;
    font-weight: 500;
    cursor: pointer;
}
.mx-sidenavmenu-items>li>a.mx-left-drawer-menu i,
.mx-sidenavmenu-items>li>a.mx-left-drawer-menu .active i {
    padding: 8px 8px;
    margin: 4px;
}
/* **********************************
Left side Navigation Side Bar menu
********************************** */
.mx-sidebar-menu:hover,
.mx-sidenavmenu-items .mx-sidebar-menu.expanded {
    width: 300px;
    overflow: hidden;
    transition-delay: 0.5s;
}
.mx-sidebar-menu {
    overflow: hidden;
    -webkit-transition: width 0.3s linear;
    transition: width 0.3s linear;
    -webkit-transform: translateZ(0) scale(1, 1);
    z-index: 10;
    /* z-index: 1000; */
    background: linear-gradient(to bottom, #504E77, #6D6CA3);
}
.mx-sidebar-menu li {
    display: block;
    position: relative;
    width: 300px;
}
.mx-sidenavmenu-items {
    width: 100%;
    list-style: none;
    display: inline-block;
    padding: 0px;
    margin: 0px;
}
.mx-sidenavmenu-items>li>a {
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 1px;
    padding: 0px;
    ;
    color: #ffffff;
    font-size: 1.2rem;
}
/* .mx-sidenavmenu-items>li+li {
    margin-left: 4px;
} */
.mx-sidenavmenu-items .menu-label {
    font-size: 0.8em;
    position: absolute;
    left: 60px;
    line-height: 50px;
}
.mx-sidenavmenu-items span.menu-label.active {
    font-weight: bold;
    font-size: .90em;
    color: #ffffff;
}
/*Scaling up the icons*/
.mx-sidenavmenu-items:hover .menu-label,
.mx-sidenavmenu-items:focus .menu-label {
    transform: translate(0);
    opacity: 1;
}
.mx-sidenavmenu-items.collapse>li>a.mx-left-drawer-menu>span.menu-label {
    opacity: 0.85;
    font-weight: 300;
}
.mx-sidenavmenu-items.collapse>li>a.mx-left-drawer-menu.active>span.menu-label {
    opacity: 1;
    font-weight: 600;
}

