.mx-widget .ripple,
.mx-widget-mini .ripple {
    position: absolute;
    width: 160px;
    height: 160px;
    z-index: -1;
    left: 50%;
    top: 50%;
    opacity: 0;
    margin: -80px 0 0 -80px;
    border-radius: 100px;
    -webkit-animation: ripple 1.8s infinite;
    animation: ripple 1.8s infinite
}
@-webkit-keyframes ripple {
    0% {
        opacity: 1;
        -webkit-transform: scale(0);
        transform: scale(0)
    }
    100% {
        opacity: 0;
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}
@keyframes ripple {
    0% {
        opacity: 1;
        -webkit-transform: scale(0);
        transform: scale(0)
    }
    100% {
        opacity: 0;
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}
.mx-widget .ripple:nth-child(2),
.mx-widget-mini .ripple:nth-child(2) {
    animation-delay: .3s;
    -webkit-animation-delay: .3s
}
.mx-widget .ripple:nth-child(3),
.mx-widget-mini .ripple:nth-child(3) {
    animation-delay: .6s;
    -webkit-animation-delay: .6s
}
