.login-page {
    width: 100%;
    height: 100vh;
    position: relative;
}

.login {
    width: 360px;
    background: #fff url(../images/blob.svg) no-repeat -13% 0;
    border-radius: 7px;
    overflow: auto;
    background-size: 75%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.login h5 {
    margin-top: 30px;
    text-align: center;
}

.login-card {
    margin-top: 20px;
    padding: 15px;
}

.login-input-box {
    position: relative;
    margin-bottom: 15px;
}

.login-input-box label {
    position: absolute;
    top: -9px;
    left: 10px;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 1px;
    background-color: #fff;
    padding: 0 3px;
}

.login-input-box input {
    padding: 5px 10px;
    width: 100%;
    border: 2px solid #dee2e6;
    border-radius: 4px;
    outline: none;
}

.login-card button:disabled {
    cursor: not-allowed;
}

.login-card button {
    width: 100%;
    border: none;
    border-radius: 4px;
    background: linear-gradient(90deg,#667eea,#764ba2);
    padding: 7px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.login p {
    margin-top: 10px;
    margin-bottom: 0;
    font-weight: 700;
    color: red;
}

.login:hover {
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12),
      0 2px 4px -1px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    transition: all 0.5s ease;
  }